* {
  box-sizing: border-box;
  padding: 0;
  margin: 0px;
  font-family: "Oswald";
  list-style-type: none;
  color: black;
}

.App {
  background-image: url("./assets/img/bg.jpg");
  background-repeat: no-repeat;
  background-attachment: scroll;
  background-size: contain;
  background-size: 70%;
  background-position-x: 100%;
  background-position-y: 10%;
}

#monselect {
  border: none;
  font-size: 1.1rem;
  font-weight: 400;
  background-color: transparent;
  width: 6rem;
}

.Blinguage-logo{
  margin: 2rem;
  width: 10rem;
  z-index: 1;
}

.Blinguage-logo:hover {
  transform: scale(1.1);
  transition: 0.4s ease;
}

.commencer {
  border: 1px solid black;
  border-radius: 10px;
  padding: 10px;
  margin: 0 auto;
  margin-left: 0;
  position: relative;
  top: 0.5rem;
  z-index: 3;
}

.commencer:hover {
  background: #372e7f;
  color: #c0eb34;
  transition: 0.4s ease;
}

a {
  font-weight: 500;
  text-decoration: none;
}

.topnav {
  margin: 1rem;
  display: flex;
  align-content: space-around;
  flex-direction: column;
}

.liContent {
  margin: 1rem;
}

.liContent:hover {
  transform: scale(1.1);
  transition: 0.4s ease;
}

main{
  position: relative;
  top: 5rem;
}

h1 {
  margin: 1rem;
}

p {
  margin: 1rem;
}

.btnStart {
  background: #372e7f;
  color: #c0eb34;
  border: none;
  border-radius: 5px;
  margin: 2rem;
  padding: 5px;
  width: 6rem;
  height: 2rem;
  z-index: 4;
}

.btnStart:hover {
  color: black;
  background: transparent;border: 1px solid black;
  border-radius: 5px;
  transform: scale(1.2);
  transform: 0.4s ease;
}


.bottomContent {
  position: relative;
  top: 2rem;
  display: flex;
  flex-direction: column;
  align-content: center;
}

.ico {
  position: relative;
  left: 25%;
}

.ico:hover {
  transform: scale(1.1);
  transition: 0.4s ease;
}

b:hover {
  color: #372e7f;
  transition: 0.4s ease;
}



@media (min-width: 600px) and (orientation: portrait) {

  .topnav {
    margin: 1rem;
  }

  p {
    width: 40rem;
  }

  .bottomContent {
    flex-direction: column;
    align-items: center;
    text-align: center;
}
}

@media (max-width: 600px) and (orientation: landscape)  {

  .topnav{
      margin: 1rem;
    }

  .bottomContent {
    flex-direction: column;
    align-items: center;
    text-align: center;
  }

}

@media (min-width: 800px) and (orientation: portrait) {

  p {
      width: 40rem;
    }

  .bottomContent {
    flex-direction: column;
    align-items: center;
    text-align: center;
  }

}

@media (min-width: 800px) and (orientation: landscape) {

  p {
      width: 40rem;
    }

  .bottomContent {
    flex-direction: column;
    align-items: center;
    text-align: center;
  }

}

@media (min-width: 950px) and (orientation: portrait) {

  p {
      width: 40rem;
    }

  .bottomContent {
    flex-direction: column;
    align-items: center;
    text-align: center;
  }

}

@media (min-width: 950px) and (orientation: landscape) {


  p {
    width: 40rem;
  }

  .bottomContent {
    flex-direction: column;
    align-items: center;
    text-align: center;
  }

  .ico {
    width: 10rem;
  }

}

@media (min-width: 1200px) {

  .Blinguage-logo {
      margin: 2rem;
      width: 15rem;
    }

  .topnav {
    position: relative;
    top: -6rem;
    margin: 1rem;
    align-content: space-around;
    flex-direction: row;
    justify-content: end;
    }

  .commencer {
    top: 0rem;
  }

  p {
    width: 40rem;
    font-size: 1.2rem;
  }

  .bottomContent {
    top: 7rem;
    flex-direction: row;
    align-items: end;
    text-align: left;
    width: 10rem !important;
  }

  span {
    margin: 1rem;
    width: 25rem;
  }

}

@media (min-width: 1600px) {

  .Blinguage-logo {
      margin: 2rem;
      width: 15rem;
    }

  .topnav {
      position: relative;
      top: -6rem;
      margin: 1rem;
      align-content: space-around;
      flex-direction: row;
      justify-content: end;
    }
  
    .commencer {
      top: 0rem;
    }
  
    p {
      width: 40rem;
      font-size: 1.2rem;
    }
  
    .bottomContent {
      top: 7rem;
      flex-direction: row;
      align-items: end;
      width: 10rem !important;
      text-align: left;
    }

    span {
      margin: 1rem;
      width: 25rem;
        }
}